module.exports = "<Navbar>\r\n  <Navbar.Header>\r\n    <Navbar.Brand>\r\n      <a href=\"#home\">Brand</a>\r\n    </Navbar.Brand>\r\n    <Navbar.Toggle />\r\n  </Navbar.Header>\r\n  <Navbar.Collapse>\r\n    <Navbar.Form pullLeft>\r\n      <FormGroup>\r\n        <FormControl type=\"text\" placeholder=\"Search\" />\r\n      </FormGroup>{' '}\r\n      <Button type=\"submit\">Submit</Button>\r\n    </Navbar.Form>\r\n  </Navbar.Collapse>\r\n</Navbar>;\r\n"


